import React from "react";
import { NavLink } from "react-router-dom"
import { Button } from "react-bootstrap"

export function CardComponent({
    icon,
    guildName,
    guildId
}) {
    return (
        <>
            <div className="col-md-4 col-10 mx-auto">
                <div className="card bg-secondary bg-gradient mycard">
                    <img src={icon} alt="" className="card-img-top icon center" />
                    <div className="card-body" >
                        <h5 className="card-title guild">{guildName}</h5>
                        {/* <p className="card-text">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quia, deleniti ad tempore omnis velit labore asperiores dolore unde amet voluptatum non quasi dignissimos repellendus eum aliquid nulla quae perferendis? Fuga.
                        </p> */}
                        <Button>
                        <NavLink to={`/dashboard/${guildId}`} className="dash_button">Dashboard</NavLink>
                        </Button>
                    </div>
                </div>
            </div>
        </>
    )
}