import React from "react";
import { Button } from 'react-bootstrap'
import styles from "./index.css"
import { CardComponent } from "..";

export function MenuComponent({ 
    guilds
 }) {

    const [hidden, setHidden] = React.useState(true)


    return  (
        <>
            <div className="my-5">
                <h1 className="text-center fs-1 fw-bold server">Servers</h1>
            </div>
            <div className="container-fluid mb-5">
                <div className="row">
                    <div className="col-10 mx-auto">
                        <div className="row gy-4">
                            {
                                guilds.map((guild) => {
                                    return <CardComponent icon={`https://cdn.discordapp.com/icons/${guild.id}/${guild.icon}.png?size=128`} guildId={guild.id} guildName={guild.name} />
                                })
                            }
                        </div>
                    </div>
                    <div className="text-center gy-10">
                        <Button href={
                            // "https://discord.com/api/oauth2/authorize?client_id=903172310150815745&permissions=8&redirect_uri=http%3A%2F%2Flocalhost%3A3001%2Fapi%2Fauth%2Fdiscord%2Fredirect&scope=bot%20applications.commands"
                            "https://discord.com/api/oauth2/authorize?client_id=903973834892382249&permissions=139589930240&scope=bot%20applications.commands"
                        } target="_blank" onClick={
                            function () {
                                setHidden(false)
                            }
                        } >Add to Server</Button>

                        <p style={{color: "white"}} hidden={hidden} className="gy-4">Please Refresh</p>
                    </div>
                </div>
            </div>
        </>
    )
}